<template>
    <div>
        <div class="col-md-3">
            <div class="list-group">
                <router-link class="list-group-item" :class="{'active' : $route.name === 'account-profile'}" :to="{name: 'account-profile'}">Profile</router-link>
                <router-link class="list-group-item" :class="{'active' : $route.name === 'account-security'}" :to="{name: 'account-security'}">Security</router-link>
                <router-link class="list-group-item" :class="{'active' : $route.name === 'extend-duration'}" :to="{name: 'extend-duration'}">Extend Duration</router-link>
                <router-link v-if="authUserStore.permission.is_admin || (authUserStore.permission.manage_user && authUserStore.profile.distributor)" class="list-group-item" :class="{'active' : $route.name === 'transfer-credit'}" :to="{name: 'transfer-credit'}">Transfer Credits</router-link>
                <router-link class="list-group-item" :class="{'active' : $route.name === 'account-vpn'}" :to="{name: 'account-vpn'}">Vpn Session</router-link>
            </div>
        </div><!-- end .col-md-3 -->
    </div>
</template>

<style>
</style>

<script>
import {mapState} from 'vuex'
export default{
  computed: {
    ...mapState({
      authUserStore: state => state.authUserStore
    })
  }
}
</script>



